import React from 'react';

class ViewPicture extends React.Component {
  constructor(props) {
	super(props);
	this.myRef = React.createRef();
  }

  componentDidUpdate() {
	  this.myRef.current.scrollTo(0, 0);
  }

  render() {
	return (
	  <div
        ref={this.myRef}
        id="modal1"
        className="modal"
		style={{width: "75%", "maxHeight": "100%", "backgroundColor": "rgba(255, 255, 255, 0.75)", position: "absolute"}}

      >
        <div className="modal-content" style={{}}>
          <a href="#!" className="modal-close waves-effect waves-green btn" style={{marginTop: "-10%"}}>
			Exit
          </a>
          <h4>{this.props.caption}</h4>
          <p>{this.props.date}</p>
		    <img alt="preview" style={{"width": "100%", "height": "100%"}} src={this.props.current} />
        </div>
      </div>
	);
  }
}

export default ViewPicture;

